import React from 'react';
import PropTypes from 'prop-types';
import {ReactComponent as ArrowRight1} from '../../assets/img/icon-arrow-right.svg';
import {ReactComponent as ArrowLeft1} from '../../assets/img/icon-arrow-left.svg';

const SliderButton = ({onButtonClick, disabled, type}) => {
  const isPrevButton = type === `prev`;

  return (
    <button
      className={`slider__button slider__button--${type}`}
      type="button"
      onClick={onButtonClick}
      disabled={disabled}
    >
      {isPrevButton ? <ArrowLeft1 className="slider__button-icon slider__button-icon--left" /> :
        <ArrowRight1 className="slider__button-icon slider__button-icon--right" />}
    </button>
  );
};

SliderButton.propTypes = {
  onButtonClick: PropTypes.func.isRequired,
  disabled: PropTypes.bool.isRequired,
  type: PropTypes.string.isRequired,
};

export default SliderButton;
